.spinner-page {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.spinner-page .spinner {
    width: 40px;
    height: 40px;
}


.main-in {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;

    position: relative;
}

.main-in .container {
    flex: 1 1;
}

.main-in aside.nav-bar {

}

.main-in aside.nav-bar.render-bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
}


#join-bx {
    padding-top: 13%;
}

#join-bx form {
    margin: auto;
    width: 300px;
    max-width: calc(100vw- 10px);
}

#join-bx form h2 {
    color: white;
    text-align: center;
    font-weight: normal;
    font-size: 32px;
}

#join-bx form input {
    display: block;
    width: 100%;
    padding: 10px 14px;
    background-color: var(--base-contrast);
    border: none;
    outline: none;
    margin-bottom: 15px;
    color: white;
    font-size: 15px;
    transition: ease-in-out 300ms background;
}

#join-bx form input:focus,
#join-bx form input:hover {
    background-color: var(--base-contrast-mid);
}

#join-bx form div {
    width: 100%;
    display: flex;
    justify-content: center;
}

#join-bx form div button {
    background-color: var(--base-color);
    outline: none;
    border: none;
    padding: .75rem 2rem;
    color: white;
    font-size: 14px;
    cursor: pointer;
}

#join-bx form div button:disabled {
    color: rgba(255, 255, 255, 0.397);
    cursor: default;
}



.form-page {
    padding-top: 10%;
}

@media screen and (max-width: 800px) {
    .form-page {
        padding-top: 13%;
    }
}

.form-page form {
    margin: auto;
    width: 350px;
    max-width: calc(100vw - 40px);
}

.form-page form h2,
.form-page form h3 {
    text-align: center;
    font-weight: normal;
    font-size: 32px;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.form-page form h2 img {
    width: 70px;
}

.form-page form h3 {
    font-size: 18px;
    margin-bottom: 2rem;
}

.form-page form input {
    display: block;
    width: 100%;
    padding: 10px 14px;
    color: var(--text-white);
    background-color: var(--base-contrast);
    border: none;
    border-radius: 2px;
    outline: none;
    margin-bottom: 15px;
    font-size: 15px;
    transition: ease-in-out 300ms background;
}

.form-page.night form input {
    color: var(--text-white);
}


.form-page form input:focus,
.form-page form input:hover {
    background-color: var(--base-contrast-op2);
}

.form-page form div {
    width: 100%;
    display: flex;
    justify-content: center;
}

.form-page form div button {
    background-color: var(--base-color);
    outline: none;
    border: none;
    border-radius: 8px;
    box-shadow: 0 3px 6px 0 var(--base-color-fade);
    padding: .65rem 2rem;
    color: white;
    font-size: 16px;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.form-page form div button .spinner {
    margin-left: 10px;
}

.form-page form div button:disabled {
    color: rgba(255, 255, 255, .34);
    cursor: default;
}

.form-page .redirect {
    margin: .5rem 0;
    margin-top: 2rem;
    display: flex;
    justify-content: flex-start;
}

.form-page .redirect a {
    text-decoration: none;
    color: var(--base-orange);
    transition: ease 300ms all;
    margin-left: 10px;
}

.form-page .redirect a:hover {
    cursor: pointer;
    transform: scale(1.03);
}

.form-page .error {
    color: var(--base-red);
    text-transform: capitalize;
    margin: 1rem 0;
    -webkit-animation-name: keyframes1;
            animation-name: keyframes1;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-timing-function: cubic-bezier(.71,.55,.62,1.57);
            animation-timing-function: cubic-bezier(.71,.55,.62,1.57);
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

@-webkit-keyframes keyframes1 {
    0% {
        opacity: 0;
        transform: scaleX(.8);
    }
    50% {
        opacity: 1;
        transform: scaleX(1);
    }
}

@keyframes keyframes1 {
    0% {
        opacity: 0;
        transform: scaleX(.8);
    }
    50% {
        opacity: 1;
        transform: scaleX(1);
    }
}

.notfound-top {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 1rem 1.2rem;
}

.notfound-top span {
    color: var(--base-white);
    transition: all ease-in-out 300ms;
    cursor: pointer;
}

.notfound-top span:hover {
    color: var(--base-light);
}

.notfound-bx {
    /* Dimension */
    width: 100vw;
    height: 100vh;
    /* Center Content */
    display: flex;
    justify-content: center;
    align-items: center;
    /* */
    font-size: calc(15vmin + 10px);
    letter-spacing: 1rem;
}

.notfound-bx span:nth-child(1),
.notfound-bx span:nth-child(3) {
    animation: upAndDown 500ms ease-in-out infinite alternate-reverse;
    font-size: 2em;
    color: var(--base-color);
}

.notfound-bx span:nth-child(3) {
    -webkit-animation-delay: 300ms;
            animation-delay: 300ms;
}

@-webkit-keyframes upAndDown {
    from {
        transform: translateY(-10px);
    } to {
        transform: translateY(10px);
    }
}

@keyframes upAndDown {
    from {
        transform: translateY(-10px);
    } to {
        transform: translateY(10px);
    }
}

.notfound-bx span:nth-child(2) {
    color: var(--base-contrast-fade);
    /* text-shadow: 0 0 3px var(--base-contrast-mid); */
    /* font-size: 1.5em; */
    transform: scale(3);
    transform-origin: 37% 50%;
    z-index: -1;
}
